import { isEmail, isEmpty, isMobilePhone } from 'validator';

const AccountValidator = (userInput) => {
    const errors = {};

    //fullname
    if (userInput.fullname != null) {
        if (userInput.fullname.length > 30) {
            errors.fullname = "H v tn khng c qu 30 k t!"
        }
    }
    //username
    if (userInput.username != null) {
        if (isEmpty(userInput.username)) {
            errors.username = "Tn ti khon l bt buc!"
        } else if (userInput.username.length > 30 || userInput.username.length < 6) {
            errors.username = "Tn ti khon phi c t 6 - 30 k t!"
        }
    }
    //password
    if (userInput.password != null) {
        if (isEmpty(userInput.password)) {
            errors.password = "Mt khu l bt buc!"
        } else if (userInput.password.length > 12 || userInput.password.length < 6) {
            errors.password = "Mt khu phi c t 6 - 12 k t!"
        }
    }
    //email
    if (userInput.email != null) {
        if (isEmpty(userInput.email)) {
            errors.email = "Email l bt buc!"
        } else if (!isEmail(userInput.email)) {
            errors.email = "Sai nh dng Email!"
        }
    }
    //birthday
    //address
    if (userInput.address != null) {
        if (userInput.address.length > 55) {
            errors.address = "a ch khng c qu 55 k t!"
        }
    }
    //phone
    if (userInput.phone_number != null) {
        if (!isMobilePhone(userInput.phone_number, 'vi-VN') && !isEmpty(userInput.phone_number)) {
            errors.phone_number = "Sai nh dng s in thoi hoc c u s l!"
        }
    }

    return errors;
}

export default AccountValidator